import { ProjectItemInfo } from "./../projects/ProjectItemInfo";
import TimeDuration from "@/generics/TimeDuration";
import { MediaItem } from "@/generics/Interfaces";

export class CVItemInfo {
  public title: string;
  public place: string;
  public infoPoints: string[];
  public timeDuration: string | TimeDuration[];
  public mediaItems: MediaItem[] | null;
  public tags: string[] | null = null;
  public projectItems: ProjectItemInfo[] | null = null;

  public constructor(
    title: string,
    place: string,
    infoPoints: string[],
    timeDuration: string | TimeDuration[],
    mediaItems: MediaItem[] | null,
    tags?: string[],
    projectItems?: ProjectItemInfo[] | null
  ) {
    this.title = title;
    this.place = place;
    this.infoPoints = infoPoints;
    this.timeDuration = timeDuration;
    this.mediaItems = mediaItems;

    if (tags) {
      this.tags = tags;
    }

    if (projectItems) {
      this.projectItems = projectItems;
    }
  }
}
