import { CVItemInfo } from "./CVItemInfo";
import { CVContent } from "@/assets/texts/json/cv-page.json";
import { MediaItem } from "@/generics/Interfaces";
import { getImage } from "../../assets/images";
import { getProjectItemsFromTitles } from "../projects/ProjectItemReader";

// Here additional information, not in the json-file is added to the media item array.
const addMissingMediaInformation = (
  mediaItems: MediaItem[] | null
): MediaItem[] | null => {
  if (mediaItems == null) {
    return null;
  }

  mediaItems.forEach((item: MediaItem) => {
    item.sourcePath = getImage(item.fileName);
  });

  return mediaItems;
};

export enum ContentType {
  Introduction = "Introduction",
  Work = "Work Experience",
  Voluntary = "Voluntary Work",
  Education = "Education and Courses"
}

export const GetCVItemList = (contentTypes: ContentType[]): CVItemInfo[] => {
  const returnArray: CVItemInfo[] = [];

  let content: any = [];

  contentTypes.forEach(contentType => {
    switch (contentType) {
      case ContentType.Introduction:
      case ContentType.Work:
      case ContentType.Voluntary:
      case ContentType.Education:
        content = CVContent[contentType];
        break;
      default:
        break;
    }
    content.forEach((object: any) => {
      returnArray.push(
        new CVItemInfo(
          object.title,
          object.place,
          object.infoPoints,
          object.timeFrame,
          addMissingMediaInformation(object.mediaItems),
          object.tags,
          getProjectItemsFromTitles(object.projectTitles)
        )
      );
    });
  });

  return returnArray;
};
