
































































import Vue from "vue";
import { CVItemInfo } from "./CVItemInfo";

export default Vue.extend({
  name: "CVFilter",
  props: {
    cvItems: {
      type: Array as () => CVItemInfo[],
      required: true
    }
  },
  data() {
    return {
      checkedNames: [] as string[]
    };
  },
  watch: {
    cvItems(newValues: string[]) {
      this.checkedNames = [];
      this.$emit("filterChanged", this);
    }
  },
  methods: {
    onFilterChanged: function(value: any) {
      const newFilterValue = value.target.checked;
      const filterName = value.target.value;

      if (this.checkedNames.includes("All")) {
        this.checkedNames = ["All"];
      }

      this.$emit("filterChanged", this);
    },
    resetFilter: function() {
      this.checkedNames = [];
      this.$emit("filterChanged", this);
    }
  },
  computed: {
    // Get the total tags and the amount of each of them.
    sortedTagsWithCounter(): Map<string, { val: number }> {
      const tagsCounter = new Map<string, { val: number }>();

      const totalTags = this.cvItems
        .map(item => item.tags)
        .filter(tag => tag != null);

      totalTags.forEach(tagList => {
        if (tagList) {
          tagList.forEach(tag => {
            if (tag) {
              tagsCounter.has(tag)
                ? tagsCounter.get(tag)!.val++
                : tagsCounter.set(tag, { val: 1 });
            }
          });
        }
      });

      const sortedMap = new Map(
        [...tagsCounter.entries()].sort((a, b) => b[1].val - a[1].val)
      );
      return sortedMap;
    }
  }
});
