















import Vue from "vue";
import { ProjectItemInfo } from "../projects/ProjectItemInfo";
import { Icons } from "@/assets/images";

export default Vue.extend({
  name: "CVProjectButton",
  props: {
    projectItemInfo: {
      type: ProjectItemInfo,
      required: true
    },
    remHeight: {
      type: Number,
      required: false,
      default: 11
    }
  },
  data() {
    return {
      Icons: Icons
    };
  },
  methods: {
    onClicked() {
      this.$emit("onClick");
    }
  },
  computed: {
    imageStyleOverride() {
      const maxHeight = this.remHeight * 0.8;
      return {
        maxHeight: maxHeight + "rem"
      };
    }
  }
});
