











import Vue from "vue";
import CVItem from "@/components/cv/CVItem.vue";
import { CVItemInfo } from "@/components/cv/CVItemInfo";
import { GetCVItemList, ContentType } from "./CVItemReader";

export default Vue.extend({
  name: "CVSection",
  components: {
    CVItem
  },
  props: {
    sectionID: {
      type: Number,
      required: true
    },
    contentType: {
      type: String as () => ContentType,
      required: true
    },
    activeFilters: {
      type: Array as () => string[],
      required: false
    }
  },

  methods: {
    isActiveItem(item: CVItemInfo) {
      // If there are no filters at all (undefined), show item.
      if (!this.activeFilters) {
        return true;
      }

      // If there are no active tag filters, show item.
      if (this.activeFilters.length === 0) {
        return true;
      }

      // If filter is active and item is without tag, do not show.
      if (!item.tags) {
        return false;
      }

      // If this item has a tag that is filtered, show the item.
      const intersectArray = this.activeFilters.filter(tag =>
        item.tags!.includes(tag)
      );
      if (intersectArray.length > 0) {
        return true;
      }

      // Any other case would be false.
      return false;
    }
  },

  data() {
    return {
      cvItems: GetCVItemList([this.contentType]) as CVItemInfo[],
      isVisible: true
    };
  },
  computed: {
    activeCVItems() {
      const activeCVItems: CVItemInfo[] = this.cvItems.filter(
        item => this.isActiveItem(item) === true
      );
      return activeCVItems;
    }
  },
  watch: {
    activeCVItems: function(newVal, oldVal) {
      this.$emit(
        "sectionChanged",
        this,
        this.activeCVItems.length,
        this.sectionID
      );
    }
  },
  mounted() {
    this.$emit(
      "sectionChanged",
      this,
      this.activeCVItems.length,
      this.sectionID
    );
  }
});
