













































import Vue from "vue";
import CVSection from "@/components/cv/CVSection.vue";
import CVFilter from "@/components/cv/CVFilter.vue";
import { GetCVItemList, ContentType } from "./CVItemReader";

const AVAILABLE_TABS: string[] = ["All", "Work", "Education", "Voluntary"];

export default Vue.extend({
  name: "CVContainer",
  components: {
    CVSection,
    CVFilter
  },
  methods: {
    onTabClicked(event: any) {
      const innerText = event.target.innerText as string;

      // No need to update if the same tab is clicked.
      if (innerText === this.activeTab) return;

      this.activeTab = innerText;
    },
    onFilterChanged(event: any) {
      this.activeFilters = event.checkedNames;
    },
    onSectionChanged(
      cvSection: any,
      numberOfActiveItems: number,
      sectionID: number
    ) {
      // Only set active items if the section's tab is active.
      if (this.isSectionTabActive[sectionID]) {
        Vue.set(this.activeSectionItems, sectionID, numberOfActiveItems);
      } else {
        Vue.set(this.activeSectionItems, sectionID, 0);
      }
    }
  },
  computed: {
    getCurrentCVContent() {
      switch (this.activeTab) {
        case AVAILABLE_TABS[0]:
          return GetCVItemList([
            ContentType.Work,
            ContentType.Education,
            ContentType.Voluntary
          ]);
        case AVAILABLE_TABS[1]:
          return GetCVItemList([ContentType.Work]);
        case AVAILABLE_TABS[2]:
          return GetCVItemList([ContentType.Education]);
        case AVAILABLE_TABS[3]:
          return GetCVItemList([ContentType.Voluntary]);
        default:
          return null;
      }
    },
    numberOfActiveItems() {
      let itemCounter = 0;
      this.activeSectionItems.forEach(itemCount => {
        itemCounter += itemCount;
      });

      return itemCounter;
    },
    isSectionTabActive(): boolean[] {
      return [
        this.activeTab == this.tabs[0] || this.activeTab == this.tabs[1],
        this.activeTab == this.tabs[0] || this.activeTab == this.tabs[2],
        this.activeTab == this.tabs[0] || this.activeTab == this.tabs[3]
      ];
    }
  },
  data() {
    return {
      contentType: ContentType,
      tabs: AVAILABLE_TABS as string[],
      activeTab: AVAILABLE_TABS[0] as string,
      activeFilters: [] as string[],
      activeSectionItems: [0, 0, 0]
    };
  }
});
