



































































import Vue from "vue";
import { CVItemInfo } from "./CVItemInfo";
import LightBoxMedia from "../generic/LightBoxMedia.vue";
import { FlexStyleProp } from "../../generics/Types";
import ProjectInfoWindow from "../projects/desktop/ProjectInfoWindow.vue";
import CVProjectButton from "./CVProjectButton.vue";
import PointsList from "../generic/PointsList.vue";

export default Vue.extend({
  name: "CVItem",
  props: {
    cvItemInfo: {
      type: CVItemInfo,
      required: true
    }
  },
  components: {
    LightBoxMedia,
    ProjectInfoWindow,
    CVProjectButton,
    PointsList
  },
  data: function() {
    return {
      projectSelected: "",
      lightBoxFlexStyle: {
        display: "flex",
        alignItems: "center",
        flexDirection: "row",
        justifyContent: "start",
        flexWrap: "wrap"
      } as FlexStyleProp
    };
  }
});
